<template>
    <h1 class="text-4xl font-bold mb-5">
        {{ this.type === "nazionale" ? "Italia" : this.type === "regioni" || this.type === "province" ? target : "" }}
    </h1>
    <div class="relative xl:w-3/5 m-auto sm:w-full">
        <canvas></canvas>
    </div>
    <button
        @click="
            this.$router.push(
                this.type === 'nazionale'
                    ? { name: 'All' }
                    : this.type === 'regioni'
                    ? { name: 'Region', params: { region: this.target } }
                    : this.type === 'province'
                    ? { name: 'Province', params: { province: this.target } }
                    : {}
            )
        "
        class="rounded m-auto mt-8 bg-blue-600 p-4 text-3xl text-white focus:outline-none focus:border-transparent transform active:scale-95"
    >
        Indietro
    </button>
</template>

<script>
import Chart from "chart.js/auto";
import zoomPlugin from "chartjs-plugin-zoom";

Chart.register(zoomPlugin);

export default {
    name: "Graph",
    props: {
        type: String,
        target: String,
    },
    data() {
        return {
            data: null,
        };
    },
    async mounted() {
        const req = await fetch(
            this.type === "nazionale"
                ? "api/nazionale/all"
                : this.type === "regioni"
                ? `api/regioni/all/${this.target}`
                : this.type === "province"
                ? `api/province/all/${this.target}`
                : ""
        );
        this.data = await req.json();

        let datas = {};
        let labels = [];
        const colors = [
            "#3366CC",
            "#DC3912",
            "#FF9900",
            "#109618",
            "#990099",
            "#3B3EAC",
            "#0099C6",
            "#DD4477",
            "#66AA00",
            "#B82E2E",
            "#316395",
            "#994499",
            "#22AA99",
            "#AAAA11",
            "#6633CC",
            "#E67300",
            "#8B0707",
            "#329262",
            "#5574A6",
            "#3B3EAC",
        ];
        for (const e of this.data) {
            let { data, ...newE } = e;
            labels.push(new Date(data).toLocaleString("default", { day: "numeric", month: "long", year: "numeric" }));

            for (const k in newE) if (datas[k]?.push(newE[k]) === undefined) datas[k] = [newE[k]];
        }

        let datasets = [];
        let i = 0;
        for (const k in datas) {
            datasets.push({
                label: k.replaceAll("_", " ").replace(/(^\w|\s\w)/g, m => m.toUpperCase()),
                data: datas[k],
                borderColor: colors[i],
                hidden: true,
            });
            i++;
        }
        datasets[0].hidden = false;

        new Chart(document.querySelector("canvas"), {
            type: "line",
            data: {
                labels: labels,
                datasets: datasets,
            },
            options: {
                normalized: true,
                responsive: true,
                lineTension: 1,
                scales: {
                    xAxis: [
                        {
                            ticks: {
                                beginAtZero: true,
                            },
                        },
                    ],
                },
                plugins: {
                    zoom: {
                        zoom: {
                            enabled: true,
                            mode: "x",
                        },
                        pan: {
                            enabled: true,
                            mode: "x",
                            speed: 40,
                        },
                    },
                    legend: {
                        position: "right",
                    },
                },
            },
        });
    },
};
</script>
